import React, { Component } from 'react';
import { Accordion, Offcanvas } from 'react-bootstrap';
import { connect } from 'react-redux';
import { feedsFilter, tagsFilter, ugcStatusFilter, textFilter, postTypeFilter, resetFilter } from '../../../../Services/Actions/assetsActions'


